import { PAGE_QUERYResult } from "@/sanity/types";
import Image from "next/image";
import { urlFor } from "@/sanity/lib/image";
import clsx from 'clsx';


type LogoListProps = Extract<
    NonNullable<NonNullable<PAGE_QUERYResult>["content"]>[number],
    { _type: "logoList" }
>;

export function LogoList({ title, logos, itemsPerRow, backgroundColor }: LogoListProps) {
    return (
        <section className={`p-12 rounded-2xl ${backgroundColor} text-center w-[1200px] mx-auto`}>
            {title && <h2 className="text-2xl font-semibold mb-6">{title}</h2>}
            <div
                className={clsx(
                    'grid gap-6 items-center justify-center',
                    'grid-cols-2 ',
                    `sm:grid-cols-${itemsPerRow} md:grid-cols-${itemsPerRow}`
                )}
            >
                {logos.map((logo) => (
                    <div key={logo._key} className="relative h-20 w-full flex gap-8 items-center justify-center">
                        <Image
                            src={urlFor(logo).width(200).url()}
                            alt={logo.alt || 'Logo'}
                            width={200}
                            height={80}
                            className="object-contain max-h-20"
                        />
                    </div>
                ))}
                
            </div>
        </section>
    );
}