import { PAGE_QUERYResult } from "@/sanity/types";
import Image from "next/image";
import { urlFor } from "@/sanity/lib/image";

type FeaturesProps = Extract<
    NonNullable<NonNullable<PAGE_QUERYResult>["content"]>[number],
    { _type: "features" }
>;

export function Features({ features, title }: FeaturesProps) {
    return (
        <section className="container w-[1200] mx-auto flex flex-col gap-8 py-16">
            {title ? (
                <h2 className="text-xl mx-auto md:text-2xl lg:text-5xl font-semibold text-slate-800 text-center max-w-3xl py-10">
                    {title}
                </h2>
            ) : null}

            {Array.isArray(features) ? (
                <div className="relative grid grid-cols-2 gap-8">
                    {/* Vertical Divider */}
                    <div className="absolute top-0 bottom-0 left-1/2 w-px bg-gray-300"></div>

                    {features.map((feature, index) => {
                        const isLastInColumn =
                            index === Math.ceil(features.length / 2) - 1 || index === features.length - 1;

                        return (
                            <div
                                key={feature._key}
                                className={`flex flex-row gap-12 p-4 ${!isLastInColumn ? 'border-b border-gray-200' : ''}`}
                            >
                                {/* Circle Background + Icon */}
                                <div className="flex items-center justify-center relative w-12 h-12">
                                    <div
                                        className={`w-12 h-12 absolute inset-0 rounded-full translate-x-4 ${feature.backgroundColor ? feature.backgroundColor : 'bg-pink-300' }`}
                                       
                                    ></div>
                                    {feature.icon && (
                                        <Image
                                            src={urlFor(feature.icon).width(100).height(100).url()}
                                            width={160}
                                            height={160}
                                            alt={feature.title}
                                            className="w-12 h-12 object-contain z-10"
                                        />
                                    )}
                                </div>

                                {/* Title and Text */}
                                <div className="flex flex-col gap-4">
                                    <h3 className="text-xl font-semibold text-slate-800">{feature.title}</h3>
                                    <p className="text-lg text-slate-600">{feature.text}</p>
                                </div>
                            </div>
                        );
                    })}
                </div>

            ) : null}
        </section>
    );
}